/* =========================
   RESET + TOKENS DE DISE√ëO
   ========================= */
:root{
  /* Triada Ruta 1 */
  --onix-quirurgico:#171F21;   /* fondo/t√≠tulos predominante */
  --blanco-perla:#EFEFEF;      /* superficies/negativo */
  --azul-cirugia:#0E5E9C;      /* acento (l√≠neas, marcadores, t√≠tulos secundarios) */
        
  /* Neutros funcionales */
  --acero-urbano:#565656;
  --surface-1:#1C2527;
  --surface-2:#202A2C;
  --divider: rgba(255,255,255,.08);
  --shadow: 0 8px 30px rgba(0,0,0,.35);

  /* tipografia corta */
   
  --font-heading: "Azo Sans","Helvetica Neue",Helvetica,Arial,system-ui,sans-serif;
  --font-body:    "Helvetica Neue",Helvetica,Arial,system-ui,sans-serif;

  /* Escalas */
  --radius-xxl: 20px; --radius-xl:16px; --radius-lg:12px; --radius-md:10px; --radius-sm:8px;
  --space-2:.5rem; --space-3:.75rem; --space-4:1rem; --space-5:1.25rem; --space-6:1.5rem; --space-8:2rem; --space-10:2.5rem; --space-12:3rem;
  --container:1200px; --transition:200ms ease;
}

/* Reset moderno */
*,*::before,*::after{ box-sizing:border-box; }
html,body{ height:100%; }
/* Base */
body{
  margin:0; color:var(--blanco-perla); background:var(--onix-quirurgico);
  font-family:var(--font-body); line-height:1.6; -webkit-font-smoothing:antialiased; text-rendering:optimizeLegibility;
}
img,video{ max-width:100%; height:auto; display:block; }
a{ color:inherit; text-decoration:none; }
button{ font:inherit; color:inherit; background:none; border:none; cursor:pointer; }

/* Utilidades comunes */
.container{ width:100%; max-width:var(--container); margin-inline:auto; padding-inline: clamp(1rem, 4vw, 2rem); }
.section{ padding-block: clamp(3rem, 1vw, 5rem); }
.muted{ color: color-mix(in oklab, var(--blanco-perla) 75%, #000 25%); }
.divider{ border-top:1px solid var(--divider); }
.rounded-xl{ border-radius:var(--radius-xxl); }
.surface{ background:var(--surface-1); border:1px solid var(--divider); }
.shadow{ box-shadow:var(--shadow); }

/* Titulares con Azo Sans */
.h1,.h2,.h3{ font-family:var(--font-heading); }
.h1{ font-weight:700; letter-spacing:-.01em; line-height:1.1; font-size:clamp(2rem,4.5vw,3.5rem); }
.h2{ font-weight:700; letter-spacing:-.005em; line-height:1.15; font-size:clamp(1.75rem,3.5vw,2.5rem); }
.h3{ font-weight:600; font-size:clamp(1.25rem,2vw,1.5rem); }

/* Eyebrow/marker (azul como l√≠nea/acento sutil) */
.eyebrow{
  font-weight:700; letter-spacing:.12em; text-transform:uppercase; font-size:12px;
  color:var(--azul-cirugia);
}

.lead{ font-size: clamp(1rem, 1.2vw, 1.125rem); color:var(--blanco-perla); opacity:.9; }

/* Botones */
.btn{
  display:inline-flex; align-items:center; gap:.625rem;
  padding:.9rem 1.15rem; border-radius: calc(var(--radius-lg) + 2px);
  border:1px solid var(--divider); transition: transform var(--transition), background var(--transition), color var(--transition), border-color var(--transition);
  font-weight:600; line-height:1; white-space:nowrap;
}
.btn--primary{ background:var(--blanco-perla); color:#0C1011; }
.btn--primary:hover{ transform: translateY(-1px); }
.btn--ghost{ background:transparent; color:var(--blanco-perla); }
.btn--ghost:hover{ background:var(--surface-2); }
/* NEW: bot√≥n acento azul (opcional para CTA) */
.btn--accent{ background:var(--azul-cirugia); color:#fff; border-color:var(--azul-cirugia); }
.btn--accent:hover{ filter:brightness(.96); transform:translateY(-1px); }

/* Chips (categor√≠as de procedimientos) */
.chips{ display:flex; flex-wrap:wrap; gap:.75rem; }
.chip{
  padding:.55rem .9rem; border-radius:999px; border:1px solid var(--divider);
  background: linear-gradient(180deg, rgba(255,255,255,.02), rgba(0,0,0,.12));
  font-weight:600; font-size:.95rem;
  
}
/* NEW: tinte azul suave al hover seg√∫n manual */
.chip:hover{
  border-color: color-mix(in oklab, var(--azul-cirugia) 35%, var(--divider) 65%);
  background: color-mix(in oklab, var(--surface-2) 80%, var(--azul-cirugia) 20%);
}

/* =========================
   NAVBAR (mejorado)
   ========================= */
.navbar{
  position: sticky; top: 0; z-index: 40;
  background: color-mix(in oklab, var(--onix-quirurgico) 92%, black 8%);
  border-bottom: 1px solid var(--divider);
  backdrop-filter: saturate(120%) blur(8px);
  transform: translateY(0);
  /* üëá transiciones m√°s largas y org√°nicas */
  transition:
    transform 420ms cubic-bezier(.22,.61,.36,1),
    opacity 420ms cubic-bezier(.22,.61,.36,1),
    background-color 320ms ease;
  will-change: transform, opacity;                     /* ‚Üë m√°s alta */
}

/* M√°s ancho que el container est√°ndar */
.navbar__wrap{ 
  display:flex; align-items:center; justify-content:space-between;
  min-height: 84px;
}
.navbar__wrap--wide{ max-width: calc(var(--container) + 140px); margin-inline:auto; }

.logo{ height: 40px; width: auto; } /* un poco m√°s grande */
.navbar__brand{ display:flex; align-items:center; gap:.75rem; font-weight:800; }

.navbar__menu{ display:flex; align-items:center; gap: clamp(.75rem, 2vw, 1.25rem); }
.navbar__cta{ margin-left: clamp(.75rem, 2.5vw, 1.5rem); display:flex; align-items:center; }

/* Estado scrolled (cuando hay desplazamiento) */
.navbar--scrolled{
  background: color-mix(in oklab, var(--onix-quirurgico) 88%, black 12%);
  box-shadow: 0 10px 30px rgba(0,0,0,.35);
}

/* Ocultar al bajar / mostrar al subir */
.navbar--hidden{ transform: translateY(-100%); }
.navbar--visible{ transform: translateY(0); }

/* Links con subrayado animado */
.navbar__link{
  position: relative;
  padding: .6rem .8rem;
  border-radius: 10px;
  color: var(--blanco-perla);
  opacity: .9;
  transition: opacity .2s ease, background .2s ease;
}
.navbar__link:hover{ background: var(--surface-2); opacity: 1; }

/* Indicador/subrayado */
.navbar__link::after{
  content:""; position:absolute; left:18%; right:18%; bottom:-10px; height:2px;
  background: var(--azul-cirugia); border-radius:2px;
  transform: scaleX(0); transform-origin:center;
  transition: transform 280ms ease;
}
.navbar__link:hover::after{ transform: scaleX(1); }
.navbar__link--active{ opacity:1; }
.navbar__link--active::after{ transform: scaleX(1); }


/* =========================
   HERO (texto izquierda + video/placeholder derecha)
   ========================= */
.hero {
  padding-block: clamp(3rem, 6vw, 6rem);
}

.hero__grid {
  display: grid;
  align-items: center;
  gap: clamp(1.25rem, 3vw, 2rem);
  /* NEW: panel derecho m√°s angosto para 9:16 */
  grid-template-columns: minmax(0, 1fr) minmax(280px, 360px);
  max-width: 1300px; /* Un poco m√°s ancho que antes */
  height: 490px;

}

@media (max-width: 1024px) {
  .hero__grid {
    grid-template-columns: 1fr;
  }
}

/* T√≠tulo sin fondo nunca */
.h1.hero__title,
.h1.hero__title *,
.h1.hero__title::before,
.h1.hero__title::after {
  background: transparent !important;
  background-color: transparent !important;
  box-shadow: none !important;
}

/* Y tambi√©n cuando haya hover en el t√≠tulo o en sus hijos */
.h1.hero__title:hover,
.h1.hero__title:hover *,
.hero__title:hover *,
.hero__title:hover::before,
.hero__title:hover::after {
  background: transparent !important;
  background-color: transparent !important;
  box-shadow: none !important;
}

/* Por si el hover viene del contenedor del hero */
.hero__content:hover .h1.hero__title,
.hero__content:hover .h1.hero__title *,
.hero__content:hover .h1.hero__title::before,
.hero__content:hover .h1.hero__title::after {
  background: transparent !important;
  background-color: transparent !important;
  box-shadow: none !important;
}

.hero__sub {
  margin-top: 20px;
  color: var(--blanco-perla);
  opacity: 0.85;
}

.hero__actions {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  margin-bottom: -5px;
}

.hero__chips {
  margin-bottom: -30px;
}

/* NUEVO: contenedor con borde animado */
.hero__content {
  margin-top: -120px;
  position: relative;
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: var(--radius-xxl);
  background: var(--surface-1);
  padding: var(--space-10);
  overflow: hidden;
  transition: border-color 0.4s ease;
}

.hero__content::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: inherit;
  border: 1px solid transparent;
  background: linear-gradient(
    120deg,
    rgba(255, 255, 255, 0.1),
    rgba(255, 255, 255, 0.02),
    rgba(255, 255, 255, 0.1)
  )
  border-box;
  mask: linear-gradient(#fff 0 0) padding-box,
        linear-gradient(#fff 0 0);
  mask-composite: exclude;
  animation: heroBorderGlow 6s linear infinite;
  pointer-events: none;
  opacity: 0.7;
}

@keyframes heroBorderGlow {
  0% {
    transform: rotate(0deg);
    opacity: 0.6;
  }
  100% {
    transform: rotate(360deg);
    opacity: 0.6;
  }
}

/* Video explicativo */
.hero__media {
  aspect-ratio: 9 / 16; /* Proporci√≥n vertical tipo video */
  width: 100%;
  max-width: 360px; /* NEW: permite hasta 360px */
  margin-left: auto; /* NEW: alinea a la derecha del grid */
  border-radius: var(--radius-xxl);
  background: radial-gradient(circle at 30% 30%, #1e2729, #141b1d);
  border: 2px solid color-mix(in oklab, var(--blanco-perla) 8%, var(--acero-urbano) 92%);
  box-shadow:
    inset 0 0 0 1px rgba(255, 255, 255, 0.04),
    0 0 12px rgba(0, 0, 0, 0.4);
  display: grid;
  place-items: center;
  color: var(--acero-urbano);
  font-weight: 600;
  font-size: 0.9rem;
  position: relative;
  overflow: hidden;
}

/* Efecto decorativo tipo doble borde */
.hero__media::before {
  content: "";
  position: absolute;
  inset: 12px;
  border-radius: calc(var(--radius-xxl) - 10px);
  border: 1px dashed rgba(255, 255, 255, 0.07);
  pointer-events: none;
}

/* versi√≥n responsiva */
@media (max-width: 1024px) {
  .hero__media {
    aspect-ratio: 9 / 16;
    width: 100%;
    max-width: 420px; /* un toque m√°s grande en m√≥viles amplios */
    margin-inline: auto;
  }
}

/* =========================
   PROCEDIMIENTOS (cards)
   ========================= */
.procs__head{ text-align:center; margin-bottom: clamp(1.5rem, 3vw, 2rem); }
.procs__sub{ max-width: 820px; margin-inline:auto; color:var(--blanco-perla); opacity:.8; }

.procs__grid{
  display:grid; gap: clamp(1rem, 2vw, 1.5rem);
  grid-template-columns: repeat(3, minmax(0, 1fr));
}
@media (max-width: 1024px){ .procs__grid{ grid-template-columns: repeat(2, 1fr); } }
@media (max-width: 640px){ .procs__grid{ grid-template-columns: 1fr; } }

.card{
  border-radius: var(--radius-xxl);
  border:1px solid var(--divider);
  background:linear-gradient(180deg, var(--surface-1), color-mix(in oklab, var(--surface-1) 70%, black 30%));
  overflow:hidden; display:flex; flex-direction:column;
}
.card__media{
  aspect-ratio:16/9; background: var(--surface-2);
  border-bottom:1px solid var(--divider);
  display:grid; place-items:center; color:var(--acero-urbano); font-weight:600;
}
.card__body{ padding: clamp(1rem, 2.2vw, 1.5rem); display:flex; flex-direction:column; gap:.5rem; }
.card__title{ margin:0; font-weight:800; font-size:1.25rem; font-family:var(--font-heading); }
.card__text{ color:var(--blanco-perla); opacity:.85; }
/* NEW: links claros por defecto, con leve tinte azul en hover */
.card__link{ margin-top:auto; display:inline-flex; align-items:center; gap:.35rem; font-weight:700; opacity:.95; color:var(--blanco-perla); }
.card__link:hover{ color: color-mix(in oklab, var(--blanco-perla) 90%, var(--azul-cirugia) 10%); }
.card__link::after{ content:"‚Üó"; font-size:.95em; transform:translateY(-1px); }

/* =========================
   ABOUT (foto doctor + texto)
   ========================= */
.about__grid{
  display:grid; gap: clamp(1rem, 2.5vw, 2rem);
  grid-template-columns: minmax(320px, 520px) 1fr;
  align-items:center;
}
@media (max-width: 1024px){ .about__grid{ grid-template-columns: 1fr; } }

.about__photo{
  border-radius: 30px;
  min-height: 600px; background:var(--surface-1);display:grid; place-items:center; color:var(--acero-urbano); font-weight:600,;
}

.imagen_about{
  border-radius: 30px;
  background-color: var(--blanco-perla)
  ;
}
.about__content .h2{ margin-top:0; }
.about__p{ color:var(--blanco-perla); opacity:.88; font-size: 19px;}
.about__cta{ margin-top: var(--space-6); }

/* =========================
   TESTIMONIOS
   ========================= */
.testis__head{ text-align:center; margin-bottom: var(--space-8); }
.testis__grid{
  display:grid; gap: clamp(1rem, 2vw, 1.25rem);
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 1024px){ .testis__grid{ grid-template-columns: repeat(2, 1fr); } }
@media (max-width: 640px){ .testis__grid{ grid-template-columns: 1fr; } }

.testi{
  background: var(--surface-1); border:1px solid var(--divider);
  border-radius: var(--radius-xxl); padding: clamp(1rem, 2.2vw, 1.5rem);
}
.testi__head{ display:flex; align-items:center; gap:.9rem; margin-bottom:.5rem; }
.avatar{
  width:40px; height:40px; border-radius:50%;
  display:grid; place-items:center; font-weight:800; background:var(--surface-2); border:1px solid var(--divider);
}
.stars{ display:inline-flex; gap:.25rem; font-size:1rem; margin-left:.5rem; }
.stars::before{ content:"‚òÖ‚òÖ‚òÖ‚òÖ‚òÖ"; letter-spacing:.15em; color: gold; }

/* =========================
   CTA IA (bloque blanco)
   ========================= */
.cta{
  background: #FBFBFB; color:#0B0D0E;
  border-top:1px solid rgba(0,0,0,.06);
  border-bottom:1px solid rgba(0,0,0,.06);
  text-align:center;
}
.cta .h2{ color:#0B0D0E; }
.cta .lead{ color:#1a1a1a; opacity:.8; }
.cta .btn--primary{ background:#0B0D0E; color:#fff; border-color:#0B0D0E; }
.cta .btn--primary:hover{ opacity:.95; transform: translateY(-1px); }
/* NEW: opci√≥n de CTA azul */
.cta .btn--accent{ background:var(--azul-cirugia); color:#fff; border-color:var(--azul-cirugia); }

/* =========================
   FOOTER ‚Äî layout exacto del mock
   ========================= */
.footer{
  background: color-mix(in oklab, var(--onix-quirurgico) 96%, black 4%);
  border-top: 1px solid var(--divider);
  padding-block: clamp(2rem, 4vw, 3rem);
}

.footer__grid{
  display:grid;
  gap: clamp(1rem, 2vw, 1.5rem);
  grid-template-columns: 1.2fr 1fr 1fr 1fr;   /* 4 columnas */
  align-items: start;
}
@media (max-width: 1024px){ .footer__grid{ grid-template-columns: 1fr 1fr; } }
@media (max-width: 640px){ .footer__grid{ grid-template-columns: 1fr; } }

.footer__brand{
  display:flex; flex-direction:column; gap:1rem;
}
.footer__logo{ width:25px; height:auto; opacity:.95; }
.footer__brand p{ margin:.35rem 0 0; opacity:.85; max-width: 40ch; }

/* t√≠tulos de columnas */
.footer__title{
  margin:.25rem 0 .75rem;
  font-weight:700;
  color: var(--blanco-perla);
}

/* listas */

.footer a{
  color: var(--blanco-perla); opacity:.9;
  transition: color .2s ease, opacity .2s ease;
}
.footer a:hover{
  color: color-mix(in oklab, var(--blanco-perla) 85%, var(--azul-cirugia) 15%);
  opacity: 1;
}

/* redes sociales */
.footer__social{
  display:flex; gap:.9rem; align-items:center; font-size: 0px;
}
.footer__social a{
  width:34px; height:34px; border-radius:50%;
  display:grid; place-items:center;
  border:1px solid var(--divider);
  color: var(--blanco-perla); opacity:.9;
  transition: background .25s ease, color .25s ease, border-color .25s ease, transform .25s ease;
}
.footer__social a:hover{
  background: color-mix(in oklab, var(--surface-2) 80%, var(--azul-cirugia) 20%);
  border-color: color-mix(in oklab, var(--divider) 60%, var(--azul-cirugia) 40%);
  transform: translateY(-1px);
}

/* address */
.footer__address a{ display:inline-block; margin-top:.25rem; }

/* l√≠nea divisora + bottom bar */
.footer .divider{ margin: 1.25rem 0; border-color: var(--divider); }
.footer__bottom{ display:flex; justify-content:center; }
.footer__bottom small{ color:var(--blanco-perla); opacity:.7; }

/* =========================
   FOCUSES / STATES
   ========================= */
a:focus-visible, button:focus-visible, .chip:focus-visible, .btn:focus-visible{
  outline:2px solid color-mix(in oklab, var(--blanco-perla) 65%, #6cf 35%);
  outline-offset: 2px;
}

/* =========================
   LAYOUT ESPEC√çFICO POR SECCI√ìN
   (para encajar con tus componentes)
   ========================= */

/* HERO spacing exacto de las capturas */
.hero .h1{ margin-bottom: var(--space-6); }
/* Procedimientos: separaciones como en screenshot */
.procs .card__title{ margin-bottom:.25rem; }
.procs .card__text{ margin-bottom:.75rem; }

/* About: tipograf√≠a y ancho √≥ptimo del p√°rrafo */
.about__content .about__p{ max-width: 62ch; }

/* Testimonios: tono de texto y altura */
.testi p{ color:var(--blanco-perla); opacity:.88; }

/* Footer: columnas y subtexto */
.footer__brand p{ margin:.35rem 0 0; opacity:.85; }

/* =========================
   PEQUE√ëAS UTILIDADES (opcionales)
   ========================= */
.hide-sm{ display:none; } @media (min-width:641px){ .hide-sm{ display:initial; } }
.gap-1{ gap:.25rem; } .gap-2{ gap:.5rem; } .gap-3{ gap:.75rem; } .gap-4{ gap:1rem; }
.flex{ display:flex; } .grid{ display:grid; }
.center{ display:grid; place-items:center; }
.max-w-prose{ max-width: 68ch; }

/* =========================
   3D CARD (tilt / parallax)
   ========================= */
.td3d-container{
  perspective: 1200px;
  display:inline-block;
}
.td3d-body{
  --rx: 0deg; --ry: 0deg; --tz: 0px;
  transform: rotateX(var(--rx)) rotateY(var(--ry)) translateZ(var(--tz));
  transform-style: preserve-3d;
  transition: transform 999ms ease, box-shadow 220ms ease, border-color 220ms ease, background 220ms ease;
  background: var(--surface-1);
  border:1px solid var(--divider);
  border-radius: var(--radius-xxl);
  box-shadow: var(--shadow);
  will-change: transform;
}
.td3d-container:hover .td3d-body{
  box-shadow: 0 16px 50px rgba(0,0,0,.45);
  border-color: color-mix(in oklab, var(--divider) 70%, var(--azul-cirugia) 30%);
}
.td3d-item{
  transform-style: preserve-3d;
}

/* Variante oscura elegante alineada al manual */
.td3d-body.is-dark{
  background: linear-gradient(180deg, var(--surface-1), color-mix(in oklab, var(--surface-1) 70%, black 30%));
}

/* Imagen dentro del body con radios y sombra suave */
.td3d-img{
  width:100%; height: 240px; object-fit:cover;
  border-radius: var(--radius-xl);
  box-shadow: 0 8px 24px rgba(0,0,0,.35);
}

/* =========================
   HOVER BORDER GRADIENT (vanilla)
   ========================= */
.hbg{
  position: relative;
  display: inline-flex;
  padding: 2px;                 /* borde externo animado */
  border-radius: 999px;         /* pill */
  overflow: hidden;
  isolation: isolate;           /* para que el halo no escape */
}

/* Bot√≥n interno (usa tus tokens) */
.hbg__btn{
  position: relative;
  z-index: 1;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: .5rem;
  padding: .9rem 1.25rem;
  border-radius: 999px;
  border: 1px solid var(--divider);
  background: var(--onix-quirurgico);
  color: var(--blanco-perla);
  font-weight: 600;
  line-height: 1;
  transition: border-color 240ms ease, background 240ms ease, color 240ms ease, transform 240ms ease;
}
.hbg__btn:hover{
  transform: translateY(-1px);
  border-color: transparent;
  transition: .5s;
}

/* Halo animado en el borde (conic-gradient) */
.hbg__halo{
  position: absolute;
  inset: 0;
  border-radius: inherit;
  background: conic-gradient(
    from var(--angle, 0deg),
    var(--azul-cirugia) 0%,
    var(--blanco-perla) 25%,
    var(--azul-cirugia) 50%,
    var(--blanco-perla) 75%,
    var(--azul-cirugia) 100%
  );
  filter: blur(6px) saturate(140%);
  opacity: 0;
  transition: opacity 320ms ease;
  z-index: 0;
}
.hbg:hover .hbg__halo{ opacity: .9; }

/* Animaci√≥n de rotaci√≥n suave */
@keyframes hbg-spin {
  0%   { --angle: 0deg; }
  100% { --angle: 360deg; }
}
.hbg:hover .hbg__halo{
  animation: hbg-spin 3s linear infinite;
}

/* Accesibilidad: respeta reduce motion */
@media (prefers-reduced-motion: reduce){
  .hbg__halo{ animation: none !important; }
}



.hbg--light .hbg__btn{
  background: var(--blanco-perla);
  color: #000000;
}


.hero__title {
  position: relative;
  z-index: 1;
  color: var(--blanco-perla);
}

/* Fondo animado */
.hero__title::before {
  content: "";
  position: absolute;
  inset: -20%;
  background: radial-gradient(circle at 20% 30%, rgba(255,255,255,0.06), transparent 60%),
              radial-gradient(circle at 80% 70%, rgba(200,200,200,0.05), transparent 60%);
  background-size: 200% 200%;
  animation: heroGlow 10s ease-in-out infinite alternate;
  z-index: -1;
  filter: blur(35px);
}

@keyframes heroGlow {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}

/* =========================
   HERO TITLE AURA EFFECT (suave al salir)
   ========================= */
.hero__title{
  background-color: var(--onix-quirurgico);
  position: relative;
  display: inline-block;
  color: var(--blanco-perla);

  /* brillo base */
  text-shadow:
    0 0 12px rgba(255,255,255,.15),
    0 0 24px rgba(255,255,255,.08);

  /* transici√≥n LARGA aqu√≠ => al sacar el mouse se desvanece */
  filter: drop-shadow(0 0 0 rgba(14,94,156,0));
  transition:
    text-shadow .75s ease,
    filter .75s ease,
    transform .45s ease;
  will-change: text-shadow, filter, transform;
}

.hero__title:hover{
  /* brillo m√°s intenso al pasar el mouse (entra r√°pido) */
  text-shadow:
    0 0 18px hsla(209, 67%, 47%, 0.651),
    0 0 42px rgba(51, 120, 223, 0.705),
    0 0 90px rgba(14,94,156,.22);
  filter: drop-shadow(0 0 22px rgba(221, 232, 240, 0.11));
  transform: translateY(-1px);

  /* en hover, que la ENTRADA sea veloz */
  transition-duration: .18s, .18s, .18s;
}

/* aura suave detr√°s que tambi√©n se desvanece */
.hero__title::after{
  content: "";
  position: absolute;
  inset: -12% -14%;
  pointer-events: none;
  z-index: -1;
  border-radius: 24px;
  background:
    radial-gradient(40% 60% at 50% 100%, rgba(100, 145, 179, 0), transparent 70%);
  opacity: 0;
  transition: opacity .75s ease;
}
.hero__title:hover::after{
  opacity: .9;
}

/* Accesibilidad */
@media (prefers-reduced-motion: reduce){
  .hero__title, .hero__title::after{
    transition: none !important;
    transform: none !important;
  }
}

