/* =========================
   3D CARDS ‚Äî FIX: estabilizar en recargas + clickeabilidad
   Pegar al final de App.css (reemplazar reglas previas .card3d)
   ========================= */

.card3d {
  position: relative;
  perspective: 1000px;                 /* contexto 3D consistente */
  transform-style: preserve-3d;
  transform: rotateX(0deg) rotateY(0deg); /* evita saltos iniciales */
  transition: transform 560ms cubic-bezier(.22,.61,.36,1);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-font-smoothing: antialiased;
}

/* Contenido interno: transiciones suaves y sin blur */
.card3d * {
  will-change: transform, opacity;
  transition:
    transform 100000ms cubic-bezier(.22,.61,.36,1),
    box-shadow 480ms cubic-bezier(.22,.61,.36,1),
    opacity 300ms linear;
  backface-visibility: hidden;
}

/* Medios (im√°genes/videos/placeholder) sin filtros que desenfoquen */
.card3d img,
.card3d video,
.card3d .card__media {
  filter: none !important;
  transform: translateZ(0);
  image-rendering: auto;
  backface-visibility: hidden;
}

/* Si el componente a√±ade overlays decorativos (::before/::after u overlays absolutos),
   nos aseguramos de que no bloqueen clicks */
.card3d::before,
.card3d::after,
.card3d [class*="overlay"],
.card3d [class*="overlay"]::before,
.card3d [class*="overlay"]::after {
  pointer-events: none !important;
}

/* Forzamos que los CTAs y enlaces queden por encima y siempre clickeables */
.card3d .btn,
.card3d a,
.card3d button,
.card3d .card__link {
  position: relative;
  z-index: 50;               /* por encima de cualquier pseudo/capa */
  pointer-events: auto;
  transform: translateZ(2px) !important; /* peque√±o plano frontal para claridad */
  backface-visibility: hidden;
}

/* Si el 3D se hace por JS (mousemove), nos aseguramos que la "salida" vuelva suave */
.card3d:not(:hover) {
  transform: rotateX(0deg) rotateY(0deg);
  transition: transform 640ms cubic-bezier(.22,.61,.36,1);
}

/* Peque√±a reducci√≥n de sensibilidad por defecto (puedes ajustar) */
:root {
  --card-tilt-default: 6deg;
}

/* Si tu JS usa variables CSS, mantenlas suaves. Ejemplo de hover opcional:
   (s√≥lo aplica si no est√°s dibujando tilt por JS) */
.card3d:hover {
  transform: rotateX(var(--card-tilt-default)) rotateY(calc(var(--card-tilt-default) * -1));
}

/* Accesibilidad: reducir animaciones si el usuario pidi√≥ reduce-motion */
@media (prefers-reduced-motion: reduce) {
  .card3d,
  .card3d * {
    transition: none !important;
  }
}

