.radio-group > small,
.radio-group small {
  display: block;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: var(--blanco-perla);
  opacity: 0.95;
  font-size: 0.92rem;
}

/* Contenedor de los radios: fila y separaci√≥n */
.radio-group .radios {
  display: flex;
  gap: 1.1rem;
  align-items: center;
  padding-top: 0.2rem;
}

/* Etiqueta que envuelve cada radio */
.radio-group label {
  display: inline-flex;
  gap: .55rem;
  align-items: center;
  cursor: pointer;
  user-select: none;
  font-weight: 600;
  color: var(--blanco-perla);
  opacity: .95;
  font-size: .95rem;
}

/* Radio nativo: tama√±o, color y apariencia moderna.
   Usamos accent-color cuando est√° soportado y fallback visual */
.radio-group input[type="radio"] {
  width: 18px;
  height: 18px;
  margin: 0;
  padding: 0;
  vertical-align: middle;
  -webkit-appearance: none;
  appearance: none;
  border: 1.6px solid color-mix(in oklab, var(--blanco-perla) 18%, var(--divider) 82%);
  border-radius: 999px;
  display: inline-grid;
  place-items: center;
  background: transparent;
  transition: border-color 180ms ease, background 180ms ease, box-shadow 180ms ease;
}

/* c√≠rculo interior: pseudo-elemento para el "punto" */
.radio-group input[type="radio"]::after {
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 999px;
  transform: scale(0);
  transition: transform 180ms cubic-bezier(.2,.9,.2,1);
  background: var(--azul-cirugia);
  box-shadow: 0 1px 0 rgba(255,255,255,0.03) inset;
}

/* estado checked: borde + punto visible */
.radio-group input[type="radio"]:checked {
  border-color: color-mix(in oklab, var(--azul-cirugia) 80%, var(--divider) 20%);
  box-shadow: 0 3px 10px rgba(0,0,0,.25) inset;
}
