  transform: scale(1);
}

/* hover/focus accesible */
.radio-group input[type="radio"]:hover {
  border-color: color-mix(in oklab, var(--azul-cirugia) 40%, var(--divider) 60%);
}
.radio-group input[type="radio"]:focus-visible {
  outline: 3px solid color-mix(in oklab, var(--azul-cirugia) 20%, var(--blanco-perla) 80%);
  outline-offset: 3px;
}

/* Ajuste en tema claro para mantener contraste (si usas data-theme="light") */
