  background: var(--onix-quirurgico);
  color: var(--blanco-perla);
  border-color: rgba(255,255,255,0.06);
  box-shadow: 0 6px 18px rgba(0,0,0,.06);
}

/* Y el inverso: forzar blanco en tema oscuro si hay atributo data-theme="dark" o .is-dark */
html[data-theme="dark"] .btn--primary,
body[data-theme="dark"] .btn--primary,
.is-dark .btn--primary,
html[data-theme="dark"] .hbg__btn,
body[data-theme="dark"] .hbg__btn,
.is-dark .hbg__btn {
  background: var(--blanco-perla);
  color: var(--onix-quirurgico);
  border-color: color-mix(in oklab, var(--divider) 80%, var(--blanco-perla) 20%);
  box-shadow: 0 6px 18px rgba(0,0,0,.18);
}

/* Hover / active con transiciones menos "brutas" */
.btn--primary:hover,
.hbg__btn:hover {
  transform: translateY(-2px);
  filter: none;
  opacity: 0.98;
}
.btn--primary:active,
.hbg__btn:active {
  transform: translateY(0);
  filter: brightness(.96);
}

/* Mantener foco accesible y con contraste */
.btn--primary:focus-visible,
.hbg__btn:focus-visible {
  outline: 3px solid color-mix(in oklab, var(--blanco-perla) 55%, var(--azul-cirugia) 45%);
  outline-offset: 4px;
}
/* ===================================
   IPHONE HERO - ANIMACIONES CREATIVAS
   =================================== */

.iphone-hero {
  min-height: 100vh;
  background: #9be6f310;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 80px 20px;
}

/* Part√≠culas flotantes de fondo */
.iphone-hero::before,
.iphone-hero::after {
  content: '';
  position: absolute;
  border-radius: 50%;
  filter: blur(120px);
  opacity: 0;
  animation: glowPulse 8s ease-in-out infinite;
  pointer-events: none;
}

.iphone-hero::before {
  width: 600px;
  height: 600px;
  background: radial-gradient(circle, #4ed61914 0%, transparent 70%);
  top: -15%;
  left: -15%;
  animation-delay: 0s;
}

.iphone-hero::after {
  width: 700px;
  height: 700px;
  background: radial-gradient(circle, rgba(218, 10, 10, 0.06) 0%, transparent 70%);
  bottom: -15%;
  right: -15%;
  animation-delay: 2s;
}

@keyframes glowPulse {
  0%, 100% { 
    opacity: 0.1;
    transform: scale(1) translate(0, 0);
  }
  50% { 
    opacity: 0.2;
    transform: scale(1.3) translate(40px, 40px);
  }
}

.iphone-hero__inner {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
  position: relative;
  z-index: 2;
}

/* ===== TAGLINE ===== */
.iphone-hero__tagline {
  color: #ffffff;
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 24px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  opacity: 0.9;
}

/* ===== T√çTULO ===== */
.iphone-hero__title {
  font-size: clamp(40px, 7vw, 64px);
  font-weight: 700;
  line-height: 1.1;
  margin-bottom: 60px;
  letter-spacing: -0.5px;
  color: #ffffff;
}

/* ===== CONTENEDOR DE IPHONES ===== */
.iphone-hero__phones {
  margin: 0 auto 60px;
  perspective: 2000px;
  border-radius: 10%;
  position: relative;
  display: inline-block;
  max-width: 100%;
  transform: scale(1.2);
}

/* Anillos pulsantes alrededor de la imagen */
.iphone-hero__phones::before,
.iphone-hero__phones::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid rgba(255, 255, 255, 0.25);
  border-radius: 60px;
  pointer-events: none;
  opacity: 0;
}

.iphone-hero__phones::before {
  width: 100%;
  height: 100%;
  animation: pulseRing 3s ease-out infinite;
}

.iphone-hero__phones::after {
  width: 100%;
  height: 100%;
  animation: pulseRing 3s ease-out infinite 1.5s;
}

@keyframes pulseRing {
  0% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) scale(1.4);
    opacity: 0;
  }
}

.iphone-hero__image {
  width: 600px;
  height: 400px;
  display: block;
  filter: drop-shadow(0 30px 60px rgba(231, 229, 229, 0.247));
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  z-index: 1;
}

/* Animaci√≥n de flotaci√≥n constante */
.is-visible .iphone-hero__image {
  animation: floatImage 6s ease-in-out infinite;
}

@keyframes floatImage {
  0%, 100% { 
    transform: translateY(0) scale(1);
  }
  50% { 
    transform: translateY(-25px) scale(1.02);
  }
}

/* Hover en la imagen */
.iphone-hero__phones:hover .iphone-hero__image {
  transform: scale(1.05) translateY(-15px);
  filter: drop-shadow(0 40px 80px rgba(255, 255, 255, 0.3));
}

/* Efecto de brillo que atraviesa */
.iphone-hero__phones {
  overflow: hidden;
}

.iphone-hero.is-visible .iphone-hero__phones::after {
  animation: pulseRing 3s ease-out infinite 1.5s, shine 5s linear infinite;
}

@keyframes shine {
  0% {
    background: linear-gradient(90deg, 
      transparent 0%, 
      transparent 40%,
      rgba(255, 255, 255, 0.2) 50%,
      transparent 60%,
      transparent 100%
    );
    transform: translate(-150%, -50%) skewX(-20deg) scale(1.5);
    border: none;
    border-radius: 0;
    width: 50%;
    height: 200%;
  }
  100% {
    transform: translate(50%, -50%) skewX(-20deg) scale(1.5);
  }
}

/* ===== DESCRIPCI√ìN ===== */
.iphone-hero__desc {
  max-width: 900px;
  margin: 0 auto;
  font-size: clamp(16px, 2vw, 18px);
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.7);
  padding: 0 20px;
}

.iphone-hero__desc .tech {
  color: #ffffff;
  font-weight: 600;
  position: relative;
  transition: color 0.3s ease;
  cursor: pointer;
}

.iphone-hero__desc .tech::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background: #ffffff;
  transition: width 0.3s ease;
}

.iphone-hero__desc .tech:hover {
  color: rgba(255, 255, 255, 0.85);
}

.iphone-hero__desc .tech:hover::after {
  width: 100%;
}

.iphone-hero__desc .sup {
  font-size: 0.6em;
  vertical-align: super;
}

/* ===== ANIMACIONES DE ENTRADA ===== */

/* Tagline - Aparece desde arriba con fade */
.iphone-hero__tagline.animate-item {
  opacity: 0;
  transform: translateY(-30px) scale(0.9);
  filter: blur(10px);
  transition: all 1s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.is-visible .iphone-hero__tagline.animate-item {
  opacity: 1;
  transform: translateY(0) scale(1);
  filter: blur(0);
}

/* T√≠tulo - Aparece con efecto de zoom y desenfoque */
.iphone-hero__title.animate-item {
  opacity: 0;
  transform: scale(0.8) translateY(50px);
  filter: blur(20px);
  transition: all 1.2s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.is-visible .iphone-hero__title.animate-item {
  opacity: 1;
  transform: scale(1) translateY(0);
  filter: blur(0);
}

/* Imagen de iPhones - Aparece rotando en 3D con explosi√≥n */
.iphone-hero__phones.animate-item {
  opacity: 0;
  transform: scale(0.5) rotateY(-45deg) rotateX(20deg) translateY(100px);
  filter: blur(25px) brightness(0.5);
  transition: opacity 1.5s cubic-bezier(0.34, 1.56, 0.64, 1),
              transform 1.5s cubic-bezier(0.34, 1.56, 0.64, 1),
              filter 1.5s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.is-visible .iphone-hero__phones.animate-item {
  opacity: 1;
  transform: scale(1.2) rotateY(0) rotateX(0) translateY(0);
  filter: blur(0) brightness(1);
  animation: phoneEntrance 0.8s ease-out forwards;
}

@keyframes phoneEntrance {
  0% {
    transform: scale(1.2) rotateY(0) rotateX(0) translateY(0);
  }
  50% {
    transform: scale(1.25) rotateY(5deg) rotateX(-3deg) translateY(-10px);
  }
  100% {
    transform: scale(1.2) rotateY(0) rotateX(0) translateY(0);
  }
}

/* Descripci√≥n - Aparece desde abajo con onda */
.iphone-hero__desc.animate-item {
  opacity: 0;
  transform: translateY(60px) scale(0.95);
  filter: blur(8px);
  transition: all 1s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.is-visible .iphone-hero__desc.animate-item {
  opacity: 1;
  transform: translateY(0) scale(1);
  filter: blur(0);
}

/* Efecto de onda expansiva cuando aparece */
.is-visible .iphone-hero__phones.animate-item::before {
  animation: pulseRing 3s ease-out infinite, expandWave 1s ease-out;
}

.is-visible .iphone-hero__phones.animate-item::after {
  animation: pulseRing 3s ease-out infinite 1.5s, expandWave 1s ease-out 0.2s;
}

@keyframes expandWave {
  0% {
    transform: translate(-50%, -50%) scale(0);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) scale(1.8);
    opacity: 0;
  }
}

/* ===== EFECTO PARALLAX CON MOUSE ===== */
.iphone-hero__tagline,
.iphone-hero__title,
.iphone-hero__phones,
.iphone-hero__desc {
  transition: transform 0.3s ease-out;
}

/* ===== PART√çCULAS DECORATIVAS ===== */
@keyframes floatParticle {
  0%, 100% { 
    transform: translateY(0) translateX(0) scale(1);
    opacity: 0;
  }
  10% { opacity: 0.5; }
  90% { opacity: 0.5; }
  100% { 
    transform: translateY(-100vh) translateX(100px) scale(1.5);
    opacity: 0;
  }
}

/* Estrellas parpadeantes */
@keyframes twinkle {
  0%, 100% { 
    opacity: 0.4; 
    transform: scale(1);
  }
  50% { 
    opacity: 1; 
    transform: scale(1.8);
  }
}

/* ===== RESPONSIVE ===== */
@media (max-width: 1024px) {
  .iphone-hero__phones {
    transform: scale(1.1);
  }
  
  .is-visible .iphone-hero__phones.animate-item {
    transform: scale(1.1) rotateY(0) translateY(0);
  }
}

@media (max-width: 768px) {
  .iphone-hero {
    padding: 60px 20px;
  }

  .iphone-hero__tagline {
    font-size: 16px;
    margin-bottom: 20px;
  }

  .iphone-hero__title {
    margin-bottom: 40px;
  }

  .iphone-hero__phones {
    margin-bottom: 40px;
    transform: scale(1);
  }
  
  .is-visible .iphone-hero__phones.animate-item {
    transform: scale(1) rotateY(0) translateY(0);
  }

  @keyframes floatImage {
    0%, 100% { 
      transform: translateY(0) scale(1);
    }
    50% { 
      transform: translateY(-15px) scale(1.01);
    }
  }
}

/* ===== ACCESIBILIDAD ===== */
@media (prefers-reduced-motion: reduce) {
  .animate-item,
  .iphone-hero__image,
  .iphone-hero__phones::before,
  .iphone-hero__phones::after,
  .iphone-hero::before,
  .iphone-hero::after {
    animation: none !important;
    transition: none !important;
  }
  
  .is-visible .animate-item {
    opacity: 1;
    transform: none;
  }
  
  .iphone-hero__phones {
    transform: scale(1.2);
  }
  
  .is-visible .iphone-hero__phones.animate-item {
    transform: scale(1.2);
  }
}


