/* ===========================
   Bot√≥n del formulario (Next)
   =========================== */

/* Tema oscuro (por defecto): fondo claro, texto oscuro */
.boton-formulario {
  --btn-bg: var(--blanco-perla, #EFEFEF);
  --btn-fg: var(--onix-quirurgico, #171F21);
  --btn-shadow: 0 12px 30px rgba(0,0,0,.28);
  --btn-shadow-hover: 0 16px 40px rgba(0,0,0,.34);

  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: .55rem;

  padding: 12px 16px;
  border: 0;
  border-radius: 12px;
  font-weight: 800;
  line-height: 1;
  cursor: pointer;

  background: var(--btn-bg) !important;  /* sobreescribe el inline */
  color: var(--btn-fg) !important;       /* sobreescribe el inline */
  box-shadow: var(--btn-shadow);
  transition:
    transform .18s ease,
    box-shadow .18s ease,
    background-color .18s ease,
    color .18s ease,
    opacity .18s ease;
}

/* Tema claro: fondo oscuro, texto claro */
.theme-light .boton-formulario,
[data-theme="light"] .boton-formulario {
  --btn-bg: var(--onix-quirurgico, #171F21);
  --btn-fg: var(--blanco-perla, #EFEFEF);
  --btn-shadow: 0 10px 28px rgba(16,24,32,.18);
  --btn-shadow-hover: 0 14px 36px rgba(16,24,32,.24);
}

/* Hover / focus */
.boton-formulario:hover:not(:disabled),
.boton-formulario:focus-visible:not(:disabled) {
  transform: translateY(-1px) scale(1.01);
  box-shadow: var(--btn-shadow-hover);
}

/* Active (click) */
.boton-formulario:active:not(:disabled) {
  transform: translateY(0) scale(.98);
}

/* Accesibilidad focus ring */
.boton-formulario:focus-visible {
  outline: 2px solid var(--azul-cirugia, #0E5E9C);
  outline-offset: 3px;
}

/* Disabled */
.boton-formulario:disabled {
  opacity: .55;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
  background: color-mix(in oklab, var(--btn-bg) 80%, transparent) !important;
  color: color-mix(in oklab, var(--btn-fg) 55%, transparent) !important;
}

/* Animaci√≥n suave del √≠cono (lucide usa currentColor) */
.boton-formulario svg {
  width: 1.1rem;
  height: 1.1rem;
  transition: transform .18s ease, opacity .18s ease;
  flex-shrink: 0;
}
.boton-formulario:hover:not(:disabled) svg {
  transform: translateX(4px);
}

/* Shine sutil en hover */
.boton-formulario::after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: inherit;
  background:
    radial-gradient(120px 120px at 20% 10%, rgba(255,255,255,.12), transparent 60%),
    radial-gradient(160px 160px at 80% 90%, rgba(255,255,255,.08), transparent 60%);
  opacity: 0;
  transition: opacity .18s ease;
  pointer-events: none;
}
.boton-formulario:hover::after { opacity: .9; }

/* Reduce motion */
@media (prefers-reduced-motion: reduce) {
  .boton-formulario,
  .boton-formulario svg,
  .boton-formulario::after {
    transition: none;
  }
}

