}

.section.marked::before{
  content: "";
  position: absolute;
  left: 50%;
  top: 0; /* si la quer√©s abajo: bottom: 0; y sac√° top */
  width: var(--section-divider-w);
  height: var(--section-divider-h);
  transform: translateX(-50%) scaleX(.6);
  transform-origin: center;
  /* degrad√© suave usando el mismo azul con bordes transparentes */
  background: linear-gradient(
    90deg,
    rgba(14, 94, 156, 0),
    var(--section-divider-color),
    rgba(14, 94, 156, 0)
  );
  opacity: 0;
  transition: opacity .45s ease .1s, transform .45s ease .1s;
  pointer-events: none;
}

