/* ===== Cross-fade Antes/Despu√©s para la galer√≠a ===== */
:root {
  --ga-fade-dur: 600ms;   /* duraci√≥n del fade */
  --ga-fade-delay: 80ms;  /* leve delay para que se sienta m√°s elegante */
  --ga-radius: 12px;
  --ga-height: 320px;
}

.ga-stack {
  position: relative;
  display: block;
  width: 100%;
  height: var(--ga-height);
  border-radius: var(--ga-radius);
  overflow: hidden;
}

.ga-img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  pointer-events: none;
  will-change: opacity, transform;
}

.ga-img--before {
  opacity: 1;
  transform: scale(1);
}

/* La de ‚Äúdespu√©s‚Äù est√° arriba y aparece con fade */
.ga-img--after {
  opacity: 0;
  transform: scale(1);
  transition:
    opacity var(--ga-fade-dur) ease var(--ga-fade-delay),
    transform var(--ga-fade-dur) ease var(--ga-fade-delay);
}

.ga-img--after.is-on {
  opacity: 1;
  transform: scale(1);
}

/* Accesibilidad: reduce animaciones si el usuario lo pide */
@media (prefers-reduced-motion: reduce) {
  .ga-img--after { transition: none; }
}

