  cursor: pointer;
  font-weight: 700;
  transition: background .2s ease, color .2s ease;
}
.faq-acc__q:hover { background: color-mix(in srgb, var(--faq-accent) 8%, transparent); }
.faq-acc__q:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px color-mix(in srgb, var(--faq-accent) 35%, transparent);
  border-radius: 10px;
}
.faq-acc__chev { transition: transform .28s ease; }
.faq-acc__item.is-open .faq-acc__chev { transform: rotate(180deg); }

/* Altura animada (0fr -> 1fr) */
.faq-acc__a {
  display: grid;
  grid-template-rows: 0fr;
  transition: grid-template-rows .42s ease, padding .28s ease;
  padding: 0 16px;
}
.faq-acc__item.is-open .faq-acc__a {
  grid-template-rows: 1fr;
  padding: 0 16px 14px;
}
.faq-acc__a-inner { overflow: hidden; }
.faq-acc__a p { margin: 8px 0 0;color:var(--blanco-perla); line-height: 1.6; }

/* Keyframes */
@keyframes faq-fadeUp {
  from { opacity: 0; transform: translateY(12px); }
  to   { opacity: 1; transform: translateY(0); }
}
@keyframes faq-cardIn {
  from { opacity: 0; transform: translateY(8px); }
  to   { opacity: 1; transform: translateY(0); }
}

/* Respeta modo oscuro/claro con tus variables globales */
:root.dark .faq-acc__a p,
.theme-dark .faq-acc__a p { color: #aeb7c1; }

/* ===== Procedimientos: animaciones y efectos ===== */

/* Entrada en cascada (stagger) para cada tarjeta */
.procs.marked .procs-card {
  animation: procs-cardIn .55s cubic-bezier(.2,.7,.25,1) var(--stagger,0ms) both;
  transform-origin: 50% 60%;
}
@keyframes procs-cardIn {
  from { opacity: 0; transform: translateY(10px) scale(.98); }
  to   { opacity: 1; transform: translateY(0) scale(1); }
}

/* Card 3D hover refinado */
.card3d { perspective: 1000px; }
.card3d .procs-media:hover .procs-img { transform: scale(1.06); }

/* Skeleton shimmer mientras carga la imagen */
.procs-media { position: relative; }
.procs-skeleton {
  position: absolute; inset: 0;
  border-radius: inherit;
  background:
    linear-gradient(90deg,
      rgba(255,255,255,0.04) 0%,
      rgba(255,255,255,0.10) 35%,
      rgba(255,255,255,0.04) 70%);
  animation: procs-shimmer 1.2s linear infinite;
}
.theme-light .procs-skeleton {
  background:
    linear-gradient(90deg,
      rgba(0,0,0,0.04) 0%,
      rgba(0,0,0,0.08) 35%,
      rgba(0,0,0,0.04) 70%);
}
.procs-skeleton.is-loaded { opacity: 0; visibility: hidden; transition: opacity .25s ease; }
@keyframes procs-shimmer {
  from { background-position: -200% 0; }
  to   { background-position: 200% 0; }
}

/* CTA con micro-animaci√≥n */
.procs-cta {
  position: relative;
  overflow: hidden;
  transition: transform .18s ease, box-shadow .25s ease, background .25s ease;
}
.procs-cta::after {
  content: "";
  position: absolute; inset: 0;
  background: radial-gradient(120px 60px at var(--mx, 50%) var(--my, 50%), color-mix(in srgb, var(--azul-cirugia, #0E5E9C) 22%, transparent) 0%, transparent 60%);
  opacity: 0; transition: opacity .25s ease;
}
.procs-cta:hover { transform: translateY(-1px); box-shadow: 0 10px 24px rgba(0,0,0,.18); }
.procs-cta:hover::after { opacity: 1; }
/* Seguir el mouse */
.procs-cta:hover {
  --mx: 50%;
  --my: 50%;
}
.procs-cta:hover {
  cursor: pointer;
}
.procs-cta:hover::after { pointer-events: none; }
.procs-cta:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px color-mix(in srgb, var(--azul-cirugia, #0E5E9C) 40%, transparent);
  border-radius: 10px;
}

/* Texto */
.procs .card__title { font-weight: 800; letter-spacing: .2px; }
.procs .card__text  { color: var(--blanco-perla); }
:root.dark .procs .card__text,
.theme-dark .procs .card__text { color:var(--blanco-perla) }

/* Grid (si ya ten√©s estilos, esto suma gaps responsivos) */
.procs .procs__grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: clamp(16px, 4vw, 28px);
}
@media (max-width: 980px) {
  .procs .procs__grid { grid-template-columns: repeat(2, minmax(0, 1fr)); }
}
@media (max-width: 640px) {
  .procs .procs__grid { grid-template-columns: 1fr; }
}


/* ===== Footer: tema, animaciones y efectos ===== */

/* Variables por tema */
:root {
  --footer-bg: var(--onix-quirurgico, #171F21);
  --footer-text: var(--blanco-perla, #EFEFEF);
  --footer-muted: #aeb7c1;
  --footer-link: var(--blanco-perla, #EFEFEF);
  --footer-accent: var(--azul-cirugia, #0E5E9C);
}
.theme-light,
:root.light,
[data-theme="light"] {
  --footer-bg: #ffffff;
  --footer-text: #0b0d0e;
  --footer-muted: #6b7280;
  --footer-link: #0b0d0e;
  --footer-accent: var(--azul-cirugia, #0E5E9C);
}

.footer {
  background: var(--footer-bg);
  color: var(--footer-text);
  position: relative;
  overflow: hidden;
  padding: 46px 0 28px;
}

/* Grid y columnas */
.footer__grid {
  display: grid;
  grid-template-columns: 1.2fr 1fr 1fr 1fr;
  gap: clamp(18px, 4vw, 36px);
  align-items: start;
}
@media (max-width: 980px) {
  .footer__grid { grid-template-columns: 1fr 1fr; }
}
@media (max-width: 640px) {
  .footer__grid { grid-template-columns: 1fr; }
}

/* Animaci√≥n de entrada (stagger) */
.f-reveal { opacity: 0; transform: translateY(10px); }
.footer.is-inview .f-reveal {
  animation: f-col-in .55s cubic-bezier(.2,.7,.25,1) var(--f-stagger, 0ms) both;
}
@keyframes f-col-in {
  to { opacity: 1; transform: translateY(0); }
}

/* Logo */
.footer__logo {
  width: 28px;
  height: auto;
  display: block;
  margin-bottom: 10px;
  transition: transform .25s ease, filter .25s ease, opacity .25s ease;
  will-change: transform;
}
.footer__logo:hover {
  transform: translateY(-2px) scale(1.02);
  filter: drop-shadow(0 8px 22px rgba(0,0,0,.25));
}

/* Texto y t√≠tulos */
.footer p,
.footer__address { color: var(--footer-muted); }
.footer__title {
  font-weight: 800;
  margin: 2px 0 8px;
}

/* Links + subrayado animado */
.footer a {
  color: var(--footer-link);
  text-decoration: none;
  position: relative;
  transition: color .2s ease;
}
.footer a::after {
  content: "";
  position: absolute;
  left: 0; right: 0; bottom: -2px;
  height: 2px;
  background: var(--footer-accent);
  transform: scaleX(0);
  transform-origin: left;
  transition: transform .22s ease;
  opacity: .9;
}
.footer a:hover::after { transform: scaleX(1); }

/* Redes sociales */
.footer__social {
  display: flex; gap: 12px; align-items: center;
  margin-top: 10px;
}
.footer__social a {
  display: grid; place-items: center;
  width: 36px; height: 36px;
  border-radius: 10px;
  background: color-mix(in srgb, var(--footer-accent) 12%, transparent);
  color: var(--footer-link);
  transition: transform .18s ease, box-shadow .22s ease, background .25s ease, color .2s ease;
}
.footer__social a:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 24px rgba(0,0,0,.18);
  background: color-mix(in srgb, var(--footer-accent) 22%, transparent);
  color: #fff;
}

/* Divider animado */
.footer__divider {
  border: 0;
  height: 2px;
  background: transparent;
  position: relative;
  margin-top: 20px;
}
.footer__divider::after {
  content: "";
  position: absolute;
  inset: 0;
  height: 2px;
  background: var(--footer-accent);
  transform: scaleX(0);
  transform-origin: left;
  transition: transform .9s cubic-bezier(.22,.9,.18,1) .15s;
}
.footer.is-inview .footer__divider::after { transform: scaleX(1); }

/* Bottom */
.footer__bottom {
  display: flex; justify-content: center; align-items: center;
  padding-top: 10px;
  color: var(--footer-muted);
  font-size: .9rem;
}

/* Listas */
.footer ul { list-style: none; padding: 0; margin: 0; margin-top: 9px;}
.footer li + li { margin-top: .9em; }

/* ====== Gallery Animations & Effects ====== */
:root {
  --azul-cirugia: #0E5E9C;
}

/* Cabecera: entra suave al montar */
.gallery.ga-mounted .ga-head {
  animation: ga-fade-up .6s cubic-bezier(.2,.7,.25,1) both;
}
.ga-filters { margin-top: .5rem; }
.ga-filter {
  position: relative;
  overflow: clip;
  transform: translateY(2px);
  transition: transform .2s ease, color .2s ease, background .25s ease;
}
.ga-filter::after {
  content: "";
  position: absolute;
  left: 12px; right: 12px; bottom: -2px;
  height: 2px;
  background: var(--azul-cirugia);
  transform: scaleX(0);
  transform-origin: left;
  transition: transform .22s ease;
  opacity: .9;
}
.ga-filter:hover { transform: translateY(0); }
.filter-btn--active.ga-filter::after { transform: scaleX(1); }

/* Grid reveal (stagger) */
.ga-grid { will-change: contents; }
.ga-card {
  opacity: 0;
  transform: translateY(12px) scale(.98);
  animation: ga-card-in .6s cubic-bezier(.2,.7,.25,1) var(--ga-delay, 0ms) both;
  /* shadow sutil que crece en hover */
  transition: filter .25s ease, transform .25s ease;
}
.ga-card.is-inview {
  /* el IntersectionObserver setea esta clase,
     pero la animaci√≥n ya corre gracias a animation-fill-mode: both */
}
.ga-card:hover .ga-stack {
  transform: scale(1.02);
  box-shadow: 0 16px 38px rgba(0,0,0,.22);
}

/* Contenedor de im√°genes antes/despu√©s */
.ga-stack {
  position: relative;
  display: block;
  width: 100%;
  height: 320px;
  border-radius: 12px;
  overflow: hidden;
  background: #101417;
  box-shadow: 0 12px 28px rgba(0,0,0,.18);
  transition: transform .25s ease, box-shadow .25s ease;
}
.ga-img {
  position: absolute; inset: 0;
  width: 100%; height: 100%;
  object-fit: cover; display: block;
  will-change: opacity, transform;
  transform: scale(1.005);
}
.ga-img--before { opacity: 1; }

/* Cross-fade m√°s suave + peque√±o zoom */
.ga-img--after {
  opacity: 0;
  transition:
    opacity .65s ease-in-out .05s,
    transform .75s cubic-bezier(.2,.7,.25,1);
  transform: scale(1.01);
}
.ga-img--after.is-on {
  opacity: 1;
  transform: scale(1.015);
}

/* Etiqueta (Antes/Despu√©s) con flotado sutil */
.ga-tag {
  position: absolute;
  bottom: 12px;
  background: rgba(0,0,0,.58);
  color: #fff;
  padding: 6px 10px;
  border-radius: 999px;
  font-weight: 700;
  font-size: .85rem;
  letter-spacing: .2px;
  box-shadow: 0 8px 20px rgba(0,0,0,.25);
  animation: ga-float 3.2s ease-in-out infinite;
}
.theme-light .ga-tag,
:root.light .ga-tag,
[data-theme="light"] .ga-tag {
  background: rgba(14,94,156,.9); /* --azul-cirugia en claro */
}

/* Focus accesible del bot√≥n de thumb */
.gallery__thumb-btn:focus-visible {
  outline: 2px solid var(--azul-cirugia);
  outline-offset: 3px;
  border-radius: 14px;
}

/* Keyframes */
@keyframes ga-card-in {
  from { opacity: 0; transform: translateY(12px) scale(.98); }
  to   { opacity: 1; transform: translateY(0)    scale(1);    }
}
@keyframes ga-fade-up {
  from { opacity: 0; transform: translateY(8px); }
  to   { opacity: 1; transform: translateY(0);   }
}
@keyframes ga-float {
  0%, 100% { transform: translateY(0); }
  50%      { transform: translateY(-2px); }
}

/* ====== Animaciones para Consulta IA ====== */
:root { --azul-cirugia:#0E5E9C; }

.ai-consult .surface {
  background: var(--surface-1, rgba(255,255,255,0.02));
  border: 1px solid var(--divider, rgba(255,255,255,.06));
}

.ai-anim { opacity: 0; }
.ai-anim.is-inview { opacity: 1; }

.ai-anim.ai-up.is-inview {
  animation: ai-fade-up .7s cubic-bezier(.2,.7,.25,1) var(--ai-delay,0ms) both;
}
.ai-anim.ai-left.is-inview {
  animation: ai-slide-left .7s cubic-bezier(.2,.7,.25,1) var(--ai-delay,0ms) both;
}
.ai-anim.ai-right.is-inview {
  animation: ai-slide-right .7s cubic-bezier(.2,.7,.25,1) var(--ai-delay,0ms) both;
}
.ai-anim.ai-pop.is-inview {
  animation: ai-pop .5s cubic-bezier(.2,.7,.25,1) var(--ai-delay,0ms) both;
}

/* Botones: micro-animaciones */
.ai-btn {
  transition: transform .15s ease, box-shadow .2s ease;
  will-change: transform;
}
.ai-btn:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 12px 28px rgba(0,0,0,.22);
}
.ai-btn-ghost { transition: transform .15s ease; }
.ai-btn-ghost:hover { transform: translateY(-1px); }

/* Resumen generado: tarjeta con leve brillo */
.ai-summary {
  background: linear-gradient(180deg, rgba(255,255,255,0.03), rgba(0,0,0,0.05));
  padding: .75rem;
  border-radius: 8px;
  border: 1px solid rgba(255,255,255,0.06);
  box-shadow: 0 10px 26px rgba(0,0,0,.18);
}

/* Loading: barra indeterminada en el tope del card */
.ai-form { position: relative; }
.ai-form.is-loading { pointer-events: none; }
.ai-form.is-loading .ai-textarea { filter: saturate(.92) opacity(.96); }
.ai-progress {
  position: absolute; inset: 0 0 auto 0;
  height: 3px;
  background:
    linear-gradient(90deg,
      transparent 0%,
      rgba(14,94,156,.15) 30%,
      var(--azul-cirugia) 50%,
      rgba(14,94,156,.15) 70%,
      transparent 100%);
  transform: translateX(-100%);
  opacity: 0;
}
.ai-form.is-loading .ai-progress {
  opacity: 1;
  animation: ai-indeterminate 1.2s linear infinite;
}

/* Historial: cada √≠tem entra con stagger */
.ai-history-item { will-change: transform, opacity; }

/* Keyframes */
@keyframes ai-fade-up {
  from { opacity: 0; transform: translateY(10px); }
  to   { opacity: 1; transform: translateY(0); }
}
@keyframes ai-slide-left {
  from { opacity: 0; transform: translateX(-14px); }
  to   { opacity: 1; transform: translateX(0); }
}
@keyframes ai-slide-right {
  from { opacity: 0; transform: translateX(14px); }
  to   { opacity: 1; transform: translateX(0); }
}
@keyframes ai-pop {
  from { opacity: 0; transform: scale(.98); }
  to   { opacity: 1; transform: scale(1); }
}
@keyframes ai-indeterminate {
  0%   { transform: translateX(-100%); }
  50%  { transform: translateX(0%); }
  100% { transform: translateX(100%); }
}

/* Respeta preferencias de reducci√≥n de movimiento */
@media (prefers-reduced-motion: reduce) {
  .ai-anim,
  .ai-anim.is-inview,
  .ai-btn,
  .ai-btn-ghost,
  .ai-progress {
    animation: none !important;
    transition: none !important;
    transform: none !important;
    opacity: 1 !important;
  }
}

:root { --azul-cirugia:#0E5E9C; }

/* ====== Aparici√≥n (form + aside) ====== */
.msf-anim { opacity: 0; }
.msf-anim.is-inview { opacity: 1; }
.msf-up.is-inview { animation: msf-fade-up .6s cubic-bezier(.2,.7,.25,1) both; }
.msf-left.is-inview { animation: msf-left .6s cubic-bezier(.2,.7,.25,1) both; }
.msf-right.is-inview { animation: msf-right .6s cubic-bezier(.2,.7,.25,1) both; }

@keyframes msf-fade-up { from {opacity:0; transform:translateY(10px)} to {opacity:1; transform:none} }
@keyframes msf-left   { from {opacity:0; transform:translateX(-12px)} to {opacity:1; transform:none} }
