  .contact__grid{ grid-template-columns: 1fr; }
}

/* Ajuste general de im√°genes en cards Home */
.Imagenes_procediminetos {
  width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
  border-radius: var(--radius-xl);
}

/* Peque√±o zoom al pasar el mouse por la imagen */
.card__media:hover .Imagenes_procediminetos {
  transform: scale(1.02);
  transition: transform .35s ease;
}

/* ===============================
   Animaciones solo para ProcedureDetail
   (scoped con .proc-detail)
   =============================== */

