}
.proc-detail .about__photo:hover {
  transform: translateY(-2px);
  transition: transform .28s ease;
}
.proc-detail .about__photo .soft-glow {
  position: absolute;
  inset: -10%;
  pointer-events: none;
  background:
    radial-gradient(120px 120px at 30% 30%, rgba(14,94,156,.18), transparent 60%),
    radial-gradient(120px 120px at 70% 70%, rgba(14,94,156,.12), transparent 60%);
  opacity: .65;
  mix-blend-mode: screen;
  z-index: -1;
}

/* CTA con leve elevaci√≥n al aparecer */
.proc-detail .testi.reveal-in {
  box-shadow: 0 10px 22px rgba(0,0,0,.18);
  transition: box-shadow .5s ease var(--d, 0ms);
}

/* Chip con pop */
.proc-detail .chip.reveal-in {
  animation: pdChipPop .5s cubic-bezier(.2,.8,.2,1) both;
}
@keyframes pdChipPop {
  0%   { transform: scale(.92); opacity: 0; }
  60%  { transform: scale(1.03); opacity: 1; }
  100% { transform: scale(1); }
}

/* Separador de p√°gina solo en detalle */
.proc-detail .container::before {
  content: "";
  display: block;
  height: 3px;
  width: 96px;
  margin: 0 0 16px 0;
  border-radius: 999px;
  background:
    radial-gradient(40px 40px at 10% 50%, rgba(14,94,156,.8), transparent),
    linear-gradient(90deg, rgba(14,94,156,1) 0%, rgba(14,94,156,.35) 70%, transparent);
  opacity: .85;
}

/* Respeta reduced motion */
@media (prefers-reduced-motion: reduce) {
  .proc-detail .reveal,
  .proc-detail .testi,
  .proc-detail .about__photo,
  .proc-detail .container::before {
    transition: none !important;
    animation: none !important;
    transform: none !important;
    opacity: 1 !important;
  }
}

/* ====== Toggle base ====== */
.theme-toggle{
  --size: 44px;
  width: var(--size);
  height: var(--size);
  border-radius: 999px;
  display: inline-grid;
  place-items: center;
  border: 1px solid var(--divider, rgba(255,255,255,.12));
  background: transparent;
  cursor: pointer;
  transition: border-color .25s ease, box-shadow .25s ease, transform .12s ease;
  color: var(--icon-fg, #EFEFEF); /* currentColor para los SVG */
  outline: none;
}
.theme-toggle:hover{ box-shadow: 0 6px 18px rgba(0,0,0,.18); }
.theme-toggle:active{ transform: scale(.98); }

/* Contenedor de iconos apilados */
.theme-toggle .icon-stack{
  position: relative;
  width: 22px; height: 22px;
}
.theme-toggle .icon{
  position: absolute; inset: 0;
  transition: transform .35s ease, opacity .35s ease, filter .35s ease;
  will-change: transform, opacity, filter;
}

/* Estado por tema usando el atributo que ya sete√°s: [data-theme] */
:root, html { /* colores por defecto (dark) */
  --icon-fg: var(--blanco-perla, #EFEFEF);
}
[data-theme="light"]{
  --icon-fg: var(--azul-cirugia, #0E5E9C);
}

/* Cross-fade / slide suave entre sol y luna */
[data-theme="dark"] .theme-toggle .sun{
  opacity: 0;
  transform: translateY(6px) scale(.9) rotate(-12deg);
  filter: blur(.1px);
}
[data-theme="dark"] .theme-toggle .moon{
  opacity: 1;
  transform: translateY(0) scale(1) rotate(0deg);
}

[data-theme="light"] .theme-toggle .sun{
  opacity: 1;
  transform: translateY(0) scale(1) rotate(0deg);
}
[data-theme="light"] .theme-toggle .moon{
  opacity: 0;
  transform: translateY(-6px) scale(.9) rotate(12deg);
  filter: blur(.1px);
}

/* A11y: menos movimiento */
@media (prefers-reduced-motion: reduce){
  .theme-toggle .icon{ transition: none !important; }
}

/* ===== Preloader global ===== */
html.no-scroll, body.no-scroll { overflow: hidden; }

.loader-overlay{
  position: fixed; inset: 0;
  z-index: 9999;
  display: grid; place-items: center;
  background: var(--onix-quirurgico, #171F21);
  color: var(--blanco-perla, #efefef);
  transition: opacity .45s ease .05s, visibility .45s ease .05s;
  opacity: 1; visibility: visible;
}
[data-theme="light"] .loader-overlay{
  background: #f7f7f8; color: #0E5E9C;  /* tema claro */
}
.loader-overlay.is-done{
  opacity: 0; visibility: hidden; pointer-events: none;
}

.loader-box{
  display: grid; gap: .5rem; place-items: center;
  padding: 1.25rem 1.5rem;
  border-radius: 14px;
  background: color-mix(in oklab, currentColor 6%, transparent);
  border: 1px solid color-mix(in oklab, currentColor 12%, transparent);
  backdrop-filter: blur(8px);
  transform: translateY(6px);
  animation: loaderUp .5s ease forwards;
}
@keyframes loaderUp{ to{ transform: translateY(0); } }

.loader-spinner{
  width: 36px; height: 36px; border-radius: 50%;
  border: 2.5px solid color-mix(in oklab, currentColor 18%, transparent);
  border-top-color: currentColor;
  animation: spin 1s linear infinite;
}
@keyframes spin { to { transform: rotate(360deg); } }

.loader-title{
  font-weight: 700; letter-spacing: .2px;
}
.loader-sub{
  font-size: .92rem; opacity: .8;
}

/* Contenedor del App: entra con fade-in */
.loader-content{ opacity: 0; transform: translateY(8px); }
.loader-content.is-shown{
  opacity: 1; transform: none;
  transition: opacity .5s ease, transform .5s ease;
}


.app-ready .section.marked.is-inview{
  opacity: 1; transform: none;
  transition: opacity .55s ease, transform .55s ease;
}

/* A11y: respeta reducir movimiento */
@media (prefers-reduced-motion: reduce){
  .loader-overlay, .loader-content.is-shown,
  .app-ready .section.marked.is-inview,
  .loader-box, .loader-spinner { animation: none !important; transition: none !important; }
}

  /* =====================================================
   GALER√çA v2 ‚Äî Glass cards + "wipe" reveal (antes‚Üídespu√©s)
   ===================================================== */
:root{
  --gal-accent: #0E5E9C;
}

/* Grid */
.gallery .gallery__grid{
  display:grid;
  gap:1.25rem;
  grid-template-columns:repeat(auto-fit, minmax(280px,1fr));
}

/* Card (glass + glow ring al hover) */
.gallery .gallery__card{
  position:relative;
  border-radius:16px;
  overflow:hidden;
  background:color-mix(in oklab, var(--surface-1, #0b1214) 90%, black 10%);
  border:1px solid color-mix(in oklab, #ffffff 10%, transparent);
  box-shadow:0 12px 28px rgba(0,0,0,.22);
  transition:transform .28s ease, box-shadow .28s ease, border-color .28s ease;
  opacity:0; transform:translateY(10px) scale(.985);
}
.section.is-inview .gallery__card{ animation:g-in .55s ease both; }
.section.is-inview .gallery__card:nth-child(2){ animation-delay:.06s }
.section.is-inview .gallery__card:nth-child(3){ animation-delay:.12s }
.section.is-inview .gallery__card:nth-child(4){ animation-delay:.18s }

@keyframes g-in{
  from{ opacity:0; transform:translateY(10px) scale(.985) }
  to{ opacity:1; transform:none }
}

.gallery .gallery__card:hover,
.gallery .gallery__card:focus-within{
  transform:translateY(-3px);
  box-shadow:0 18px 44px rgba(0,0,0,.28);
  border-color:color-mix(in oklab, #ffffff 16%, transparent);
}

/* Anillo de realce (conic) */
.gallery .gallery__card::before{
  content:"";
  position:absolute; inset:0; border-radius:18px; padding:1px;
  background:conic-gradient(from 180deg, transparent 0 34%, var(--gal-accent), transparent 70% 100%);
  -webkit-mask:
    linear-gradient(#000 0 0) content-box, 
    linear-gradient(#000 0 0);
  -webkit-mask-composite: xor; mask-composite: exclude;
  opacity:0; transition:opacity .35s ease;
  pointer-events:none;
}
.gallery .gallery__card:hover::before{ opacity:.6 }

/* Thumbnail con efecto "wipe" */
.gallery .gallery__thumb{
  position:relative; isolation:isolate;
  aspect-ratio: 3 / 2;            /* nuevo ratio, m√°s panor√°mico */
  overflow:hidden;
  background:linear-gradient(180deg, rgba(255,255,255,.02), rgba(0,0,0,.06));
  border-bottom:1px solid color-mix(in oklab, #ffffff 10%, transparent);
}

/* brillo diagonal sutil al hover */
.gallery .gallery__thumb::after{
  content:""; position:absolute; inset:0; pointer-events:none; z-index:2;
  background:linear-gradient(110deg, transparent 45%, rgba(255,255,255,.18) 50%, transparent 55%);
  transform:translateX(-120%); transition:transform .6s ease;
  mix-blend-mode:soft-light;
}
.gallery .gallery__thumb:hover::after{ transform:translateX(120%) }

/* Pila de im√°genes */
.gallery .ga-stack .ga-img{
  position:absolute; inset:0; width:100%; height:100%; object-fit:cover;
  transition:filter .35s ease, transform .35s ease, clip-path .55s ease, opacity .35s ease;
  will-change:filter, transform, clip-path, opacity;
}
.gallery .ga-img--before{ filter:saturate(.94) contrast(1.02); }
.gallery .ga-img--after{
  /* NUEVO: parte recortada (0% visible) y se despliega como "wipe" */
  clip-path: inset(0 100% 0 0);
  opacity:1;                 /* no necesitamos fade total, el clip se encarga */
}
.gallery .ga-img--after.is-on{ clip-path: inset(0 0 0 0) }
.gallery .gallery__thumb:hover .ga-img{ transform:scale(1.04) }

/* Etiqueta */
.gallery .gallery__tag{
  position:absolute; z-index:3; right:10px; bottom:10px;
  padding:.28rem .6rem; border-radius:999px; font-size:.78rem; font-weight:800;
  background:var(--gal-accent); color:#fff;
  box-shadow:0 8px 18px rgba(0,0,0,.25);
  width: 7em;
}

/* Meta */
.gallery .gallery__meta{
  padding:.8rem .95rem .95rem;
  background:linear-gradient(180deg, rgba(255,255,255,.03), rgba(0,0,0,.08));
}
.gallery .gallery__meta strong{ font-weight:800; letter-spacing:.2px; }
.gallery .gallery__meta .muted{ opacity:.82 }

/* Modo claro */
[data-theme="light"] .gallery .gallery__card{
  background:#fff; border-color:#e8edf3;
  box-shadow:0 12px 28px rgba(10,20,30,.08);
}
[data-theme="light"] .gallery .gallery__thumb{
  background:linear-gradient(180deg,#fbfdff,#f4f7fb);
  border-bottom-color:#e8edf3;
}
[data-theme="light"] .gallery .gallery__meta{
  background:linear-gradient(180deg,#fff,#f7fafc);
}

