  height: 100%;
  object-fit: cover;
}

/* Bot√≥n debajo */
.final-section__button {
  margin-top: 0.25rem;
}

/* ====== FONDO NUBLADO M√ÅS OSCURO ====== */

.cloudy-bg {
  background:
    radial-gradient(circle at 10% 0%, rgba(255, 255, 255, 0.04), transparent 55%),
    radial-gradient(circle at 90% 10%, rgba(255, 255, 255, 0.03), transparent 55%),
    radial-gradient(circle at 0% 100%, rgba(255, 255, 255, 0.03), transparent 55%),
    radial-gradient(circle at 100% 100%, rgba(255, 255, 255, 0.04), transparent 55%),
    linear-gradient(135deg, #05070a, #0d1117);   /* mucho m√°s oscuro */
}

/* nubes suaves animadas */
.cloudy-bg::before {
  content: "";
  position: absolute;
  inset: -40%;
  background:
    radial-gradient(circle at 20% 20%, rgba(255, 255, 255, 0.05), transparent 60%),
    radial-gradient(circle at 80% 40%, rgba(255, 255, 255, 0.03), transparent 60%),
    radial-gradient(circle at 30% 80%, rgba(255, 255, 255, 0.04), transparent 60%);
  opacity: 0.4;
  filter: blur(32px);
  animation: clouds-move 22s linear infinite alternate;
  pointer-events: none;
}

/* BORDE LUMINOSO DEL VIDEO */
.video-glow {
  border: 1px solid rgba(255, 255, 255, 0.25);
  box-shadow:
    0 0 24px rgba(14, 94, 156, 0.8),
    0 0 90px rgba(14, 94, 156, 0.5);
  backdrop-filter: blur(18px);
  animation: video-pulse 6s ease-in-out infinite alternate;
}

@keyframes video-pulse {
  0% {
    box-shadow:
      0 0 18px rgba(14, 94, 156, 0.5),
      0 0 40px rgba(14, 94, 156, 0.25);
  }
  100% {
    box-shadow:
      0 0 32px rgba(14, 94, 156, 0.95),
      0 0 95px rgba(14, 94, 156, 0.55);
  }
}

/* Animaci√≥n nubes */
@keyframes clouds-move {
  0% {
    transform: translate3d(-10px, 0, 0) scale(1.04);
  }
  100% {
    transform: translate3d(10px, -10px, 0) scale(1.07);
  }
}

/* Responsive */
@media (max-width: 768px) {
  .final-section {
    min-height: 70vh;
    padding: 4rem 1.25rem;
  }

  .final-section__inner {
    gap: 2rem;
  }

  .final-section__video-vertical {
    max-width: 320px;       /* un poquito m√°s chico en mobile */
    border-radius: 22px;
  }
}

/* Capa de part√≠culas sobre el fondo nublado */
.final-section__particles {
  position: absolute;
  inset: 0;
  pointer-events: none;
  z-index: 0;

  /* MUCHAS m√°s part√≠culas, m√°s grandes y visibles */
  background-image:
    radial-gradient(3px 3px at 8% 15%, rgba(255, 255, 255, 0.85), transparent),
    radial-gradient(3px 3px at 22% 80%, rgba(14, 94, 156, 0.9), transparent),
    radial-gradient(4px 4px at 40% 30%, rgba(255, 255, 255, 0.8), transparent),
    radial-gradient(3px 3px at 60% 75%, rgba(14, 94, 156, 0.85), transparent),
    radial-gradient(4px 4px at 78% 20%, rgba(255, 255, 255, 0.9), transparent),
    radial-gradient(3px 3px at 92% 60%, rgba(14, 94, 156, 0.9), transparent),
    radial-gradient(2px 2px at 50% 50%, rgba(255, 255, 255, 0.75), transparent);
  opacity: 0.9;
  animation: particles-float 20s linear infinite alternate;
}

/* Extra: segunda capa de part√≠culas para m√°s profundidad */
.final-section__particles::before {
  content: "";
  position: absolute;
  inset: -10%;
  background-image:
    radial-gradient(2px 2px at 15% 40%, rgba(255, 255, 255, 0.6), transparent),
    radial-gradient(2px 2px at 35% 15%, rgba(14, 94, 156, 0.7), transparent),
    radial-gradient(3px 3px at 55% 85%, rgba(255, 255, 255, 0.65), transparent),
    radial-gradient(2px 2px at 75% 45%, rgba(14, 94, 156, 0.7), transparent),
    radial-gradient(2px 2px at 90% 30%, rgba(255, 255, 255, 0.6), transparent);
  opacity: 0.7;
  filter: blur(0.5px);
  animation: particles-float-alt 26s linear infinite alternate;
}

/* Aseguramos que el contenido quede por encima de las part√≠culas */
.final-section__inner {
  position: relative;
  z-index: 1;
}

/* Animaci√≥n principal de flotaci√≥n de part√≠culas */
@keyframes particles-float {
  0% {
    transform: translate3d(0, 0, 0) scale(1);
  }
  50% {
    transform: translate3d(-12px, -10px, 0) scale(1.03);
  }
  100% {
    transform: translate3d(10px, -18px, 0) scale(1.05);
  }
}

/* Segunda animaci√≥n, m√°s lenta y en otra direcci√≥n */
@keyframes particles-float-alt {
  0% {
    transform: translate3d(10px, 5px, 0) scale(1.02);
  }
  50% {
    transform: translate3d(-8px, -12px, 0) scale(1.05);
  }
  100% {
    transform: translate3d(6px, -4px, 0) scale(1.02);
  }
}

