.hero__video.is-visible {
  animation: heroVideoDropIn 0.9s cubic-bezier(0.2, 0.75, 0.25, 1) forwards;
}

@keyframes heroVideoDropIn {
  0% {
    opacity: 0;
    transform: translateY(-160%) rotate(-14deg);
  }
  55% {
    /* baja y se pasa un poquito, girando a la derecha */
    opacity: 1;
    transform: translateY(10%) rotate(6deg);
  }
  100% {
    /* se acomoda en su lugar final */
    opacity: 1;
    transform: translateY(0) rotate(0deg);
  }
}

.hero__media {
  position: relative;
}

.hero__video {
  width: 100%;
  border-radius: 18px;
  display: block;
  box-shadow:
    0 24px 60px rgba(0, 0, 0, 0.75),
    0 0 0 1px rgba(255, 255, 255, 0.06);
  opacity: 0;
  transform-origin: center;
  will-change: transform, opacity;

  /* Estado inicial: m√°s suave, un poco m√°s cerca */
  transform: translateY(-140%) rotate(-10deg) scale(0.96);
}

.hero__video.is-visible {
  /* üîπ M√°s lenta + rebote elegante */
  animation: heroVideoDropIn 1.9s cubic-bezier(0.19, 1, 0.22, 1) forwards;
}

@keyframes heroVideoDropIn {
  0% {
    opacity: 0;
    transform: translateY(-140%) rotate(-10deg) scale(0.96);
  }
  55% {
    /* Llega a posici√≥n con un leve overshoot */
    opacity: 1;
    transform: translateY(6%) rotate(4deg) scale(1.02);
  }
  75% {
    /* Primer rebote suave hacia arriba */
    transform: translateY(-3%) rotate(-1.5deg) scale(0.995);
  }
  90% {
    /* Segundo rebote casi imperceptible */
    transform: translateY(1.5%) rotate(0.6deg) scale(1.002);
  }
  100% {
    /* Posici√≥n final estable y limpia */
    opacity: 1;
    transform: translateY(0) rotate(0deg) scale(1);
  }
}

/* T√≠tulo FAQ */
.titulo_preguntasfrecuentes {
  position: relative;
  display: inline-block;
  margin: 0 0 0.5rem;
  font-size: clamp(2.2rem, 4vw, 3rem);
  font-family: var(--font-heading, "Azo Sans", system-ui, -apple-system, BlinkMacSystemFont, sans-serif);
  font-weight: 600;
  line-height: 1.1;
  color: var(--blanco-perla, #efefef);
  letter-spacing: 0.08em;
  text-transform: uppercase;
  overflow: visible;
}

/* Palabra "Respuestas" con highlight en azul cirug√≠a */
.titulo_preguntasfrecuentes__accent {
  background: linear-gradient(
    90deg,
    var(--azul-cirugia, #0e5e9c),
    rgba(14, 94, 156, 0.3)
  );
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  position: relative;
}

/* Glow suave detr√°s de todo el t√≠tulo */
.titulo_preguntasfrecuentes::before {
  content: "";
  position: absolute;
  inset: 55% -0.25em -0.25em;
  background: radial-gradient(
    circle at 0% 100%,
    rgba(14, 94, 156, 0.32),
    transparent 60%
  );
  opacity: 0;
  transform: translateY(12px) scaleX(0.85);
  transform-origin: left;
  border-radius: 999px;
  pointer-events: none;
}

/* Subrayado din√°mico */
.titulo_preguntasfrecuentes::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -0.45rem;
  width: 58%;
  height: 2px;
  border-radius: 999px;
  background: linear-gradient(
    90deg,
    var(--azul-cirugia, #0e5e9c),
    rgba(14, 94, 156, 0)
  );
  transform-origin: left;
  transform: scaleX(0);
  opacity: 0;
}

/* Animaci√≥n de entrada cuando la secci√≥n FAQ tiene .is-inview (ya la manej√°s con IntersectionObserver) */
.faq.is-inview .titulo_preguntasfrecuentes {
  animation: faqTitleIn 0.95s cubic-bezier(0.23, 1, 0.32, 1) forwards;
}

.faq.is-inview .titulo_preguntasfrecuentes::before {
  opacity: 1;
  transform: translateY(0) scaleX(1);
  transition:
    opacity 0.8s ease-out 0.15s,
    transform 0.8s ease-out 0.15s;
}

.faq.is-inview .titulo_preguntasfrecuentes::after {
  opacity: 1;
  transform: scaleX(1);
  transition:
    opacity 0.7s ease-out 0.2s,
    transform 0.7s ease-out 0.2s;
}

/* Keyframes del t√≠tulo: sube, aparece y cierra un poco el tracking */
@keyframes faqTitleIn {
  0% {
    opacity: 0;
    transform: translateY(18px);
    letter-spacing: 0.18em;
  }
  60% {
    opacity: 1;
    transform: translateY(0);
    letter-spacing: 0.08em;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
    letter-spacing: 0.04em;
  }
}

