/* 1) Quitar cualquier borde / l√≠nea del hero */
.hero {
  margin-top: 0;                 /* por las dudas */
  border: none !important;
  border-top: none !important;
  outline: none !important;
}

/* 2) Apagar cualquier pseudo-elemento que dibuje l√≠neas en el hero */
.hero::before,
.hero::after {
  content: none !important;
  display: none !important;
  border: 0 !important;
  background: none !important;
  box-shadow: none !important;
}

/* 3) Por si la l√≠nea viene de un divisor entre header y hero */
header + .hero,
header + main,
header + section {
  border-top: none !important;
}

/* 4) Extra por si es un <hr> metido entre medio */
header + hr,
main > hr:first-child,
section > hr:first-child {
  display: none !important;
}

